.card {
    background: #fff;
    border-radius: 2px;
    height: 240px;
    margin: 1rem;
    margin-top: 10px;
    position: relative;
    width: 270px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    overflow: hidden;
  }
  
  /* Slide */
   .hover img {
      margin-left: 10px;
      -webkit-transform: scale(1.5);
      transform: scale(1.5);
      -webkit-transition: .3s ease-in-out;
      transition: .3s ease-in-out;
  }
  .hover img:hover  {
      margin-left: -30px;
  
  }
.wrapper {
    padding-top: 10px;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    overflow: auto;
    margin-left: auto;
    margin-right: auto;
  
  }
